import { observer } from 'mobx-react-lite'
import { FC, useContext, useEffect } from 'react'
import MainLayout from '../components/layouts/MainLayout'
import { CERTIFICATE_ROUTE, HOME_ROUTE, LIST_ROUTE, MO_SETTINGS_ROUTE } from '../utils/consts'
import { Button } from 'primereact/button'
import { Card } from 'primereact/card'
import { DataTable } from 'primereact/datatable'
import { Column } from 'primereact/column'
import { Divider } from 'primereact/divider'
import { Context } from '..'
import { ProgressSpinner } from 'primereact/progressspinner'
import { IRouteProps } from '../models/IRouteProps'
import { Operation, OperationType } from '../store/certificateStore'



const MainPage: FC<IRouteProps> = (props: IRouteProps) => {
  const {userStore, certificateStore, layoutStore} = useContext(Context)
  useEffect(()=>{
    if (userStore.userInfo)     
      certificateStore.userInfo = userStore.userInfo
  },[certificateStore, userStore.userInfo])
  useEffect(()=>{         
      if  (certificateStore.userInfo && !certificateStore.isLoading) {
        certificateStore.operation = new Operation(OperationType.FILTERING)        
        if (props.location.search){
          const _params = props.location.search.replace("?","").split("&") 
          let _filters = {} as any
          _params.forEach(param=>{
          const pair = param.split("=")
          _filters[pair[0]]=pair[1]
          })        
          certificateStore.filters = {..._filters} 
        } else certificateStore.filters = {}
        
      }              
  },[certificateStore, certificateStore.userInfo, layoutStore, props.location.search])
  // useEffect(()=>{         
  //     if  (layoutStore.isLoading && certificateStore.count) {        
  //       layoutStore.isLoading = false        
  //     }              
  // },[certificateStore, certificateStore.count, layoutStore])
  
  const news =[{version:"3.005",record:"   ()  ;    ;        ."},{version:"3.004",record:"    ;    _     ."},{version:"3.003",record:"  .    , ,   "},{version:"3.002",record:"     "},
  {version:"3.001",record:"    "},
  {version:"3.000",record:"   CDA_R2  3"}]
  const layoutParams= {
    title: '',     
    url: HOME_ROUTE,
    content: userStore ? ( 
      <>     
        <div className="p-d-flex p-flex-column p-jc-around p-flex-md-row p-flex-wrap">
          <Button style={{minWidth:'243px'}} className="p-mr-2 p-mb-2 p-shadow-3" label=" "  title="  " 
          onClick={(e)=>{userStore.history().push(`${CERTIFICATE_ROUTE}/${certificateStore.cert.id}?q=0`)}}/>
            <Button style={{minWidth:'243px'}} className="p-button-secondary p-mr-2 p-mb-2 p-shadow-3" label="" title="  " onClick={(e)=>{userStore.history().push(`${MO_SETTINGS_ROUTE}/${userStore.userInfo?.organization.id}`)}}/>
            <Button style={{minWidth:'243px'}} className="p-button-secondary p-mr-2 p-mb-2 p-shadow-3" id="reports" label="" title=" " />
            <Button style={{minWidth:'243px'}} className="p-mr-2 p-mb-2 p-shadow-3" label=" " /> 
            <Button className="p-button-secondary p-mr-2 p-mb-2 p-shadow-3" 
            id="repBt" style={{minWidth:'243px'}} label="" title="    " />                     
            <Button style={{minWidth:'243px'}} className="p-button-success p-mr-2 p-mb-2 p-shadow-3" label=" " title="  " onClick={(e)=>{userStore.history().push(LIST_ROUTE)}}/>            
        </div> 
        <Divider style={{marginTop:'1rem', marginBottom:'1rem'}}/>
        <div className="p-d-flex p-flex-column p-jc-around p-flex-md-row p-flex-wrap">
          <Card className="p-mr-2 p-mb-2" title=" " >
            <DataTable className="p-datatable-sm" value={news} responsiveLayout='scroll'>
              <Column field="version" header=""></Column>
              <Column field="record" header=" "></Column>
            </DataTable> 
          </Card>
        </div>                  
      </>
    ) : (<ProgressSpinner/>)   
  }  
  return (
    <>
      <MainLayout {...layoutParams} />
    </>
  )
}
export default observer(MainPage)